@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
}

.App {
  min-width: 740px;
  padding: 30px;
  background-color: #f1f3ff;
}

.App-loading {
  width: 100%;
  height: 100vh;
  color: #fff;
  background-color: #726beb;
}

.load-text {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 30px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-weight: bold;
}

.dot-container {
  position: absolute;
  left: 50%;
  top: -20px;
  width: 60%;
  height: 20px;
  display: flex;
  justify-content: space-between;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}

.dot-container .dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: #fff;
  border-radius: 50%;
  -webkit-animation: loadingAnimation 0.6s infinite alternate;
          animation: loadingAnimation 0.6s infinite alternate;
}

.load-text .dot:nth-child(2) {
  -webkit-animation: loadingAnimation 0.6s 0.3s infinite alternate;
          animation: loadingAnimation 0.6s 0.3s infinite alternate;
}

.load-text .dot:last-child {
  -webkit-animation: loadingAnimation 0.6s 0.6s infinite alternate;
          animation: loadingAnimation 0.6s 0.6s infinite alternate;
}

@-webkit-keyframes loadingAnimation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes loadingAnimation {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
.movie-item {
  background-color: #fff;
  margin: 30px auto 0;
  max-width: 660px;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 8px 30px 0 rgba(26, 26, 26, 0.12);
}

.flex-row {
  display: flex;
  align-items: center;
}

.movie-ranking {
  /* color: #4d4d4d; */
  display: inline-block;
  font-size: 20px;
  margin-bottom: 4px;
  color: #726beb;
}

.movie-ranking:after {
  display: block;
  margin-top: 2px;
  margin-bottom: 8px;
  width: 100%;
  height: 2px;
  background-color: #726beb;
  ;
  content: '';
}

.movie-poster {
  width: 210px;
  flex-shrink: 0;
  margin-right: 20px;
}

.movie-poster img {
  width: 100%;
  overflow: hidden;
  border-radius: 6px;
}

.movie-info-container .movie-info-title {
  font-size: 28px;
  font-weight: bold;
}

.movie-info-container .movie-info-rating {
  display: flex;
  align-items: center;
  margin: 8px 0 22px;
  font-size: 14px;
  color: #999;
}

.movie-info-container .movie-info-rating .rating-stars {
  margin-right: 2px;
}

.movie-info-container .movie-info-rating .rating-stars:before {
  display: inline-block;
  color: #f3bc32;
  font-family: serif;
}

.movie-info-container .movie-info-rating .rating-stars.star-1:before {
  content: '\2605\2606\2606\2606\2606';
}

.movie-info-container .movie-info-rating .rating-stars.star-2:before {
  content: '\2605\2605\2606\2606\2606';
}

.movie-info-container .movie-info-rating .rating-stars.star-3:before {
  content: '\2605\2605\2605\2606\2606';
}

.movie-info-container .movie-info-rating .rating-stars.star-4:before {
  content: '\2605\2605\2605\2605\2606';
}

.movie-info-container .movie-info-rating .rating-stars.star-5:before {
  content: '\2605\2605\2605\2605\2605';
}

.movie-info-container .movie-info-genere {
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
}

.movie-info-container .movie-info-genere .movie-genere-tag {
  font-size: 15px;
  /* color: #726beb; */
  color: #525252;
  margin-right: 10px;
  line-height: 140%;
}

.movie-info-container .movie-summary {
  border-top: 1px solid #ddd;
  margin-top: 20px;
  padding-top: 20px;
  font-size: 13px;
  color: #333;
  line-height: 145%;
  height: 100px;
  overflow: hidden;
  padding-right: 10px;
}

.btn-more {
  color: #4d4d4d;
  letter-spacing: 0.5px;
  font-weight: bold;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 11px;
  margin-top: 4px;
  cursor: pointer;
}

.btn-more:hover {
  text-decoration: underline;
}
